'use client';

import React, { useEffect, useState, useCallback } from 'react';
import { useRouter } from 'next/navigation';
import { services } from '@/services/api';
import { Plano, CompraPlano } from '@/services/api/plano.service';
import { formatCurrency } from '@/utils/format';
import { 
  CreditCardIcon,
  CheckCircleIcon,
  ClockIcon
} from '@heroicons/react/24/outline';
import Input from '@/components/form/input/InputField';
import Button from '@/components/ui/button/Button';
import Pagination from '@/components/common/Pagination';
import { Modal } from '@/components/ui/modal';
import Label from '@/components/form/Label';
import Select from '@/components/ui/Select';
import toast from 'react-hot-toast';
import { Card } from '@/components/ui/card';
import Badge from '@/components/ui/badge/Badge';

export default function ContratarPlanosPage() {
  const router = useRouter();
  const [planos, setPlanos] = useState<Plano[]>([]);
  const [loading, setLoading] = useState(true);
  const [searchTerm, setSearchTerm] = useState('');
  const [currentPage, setCurrentPage] = useState(1);
  const [totalPages, setTotalPages] = useState(1);
  const [itemsPerPage, setItemsPerPage] = useState(9);
  const [statusFilter, setStatusFilter] = useState('ativo');
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [selectedPlano, setSelectedPlano] = useState<Plano | null>(null);
  const [contratando, setContratando] = useState(false);

  const loadPlanos = useCallback(async () => {
    try {
      setLoading(true);
      const response = await services.plano.list({
        page: currentPage,
        perPage: itemsPerPage,
        search: searchTerm || undefined,
        ativo: statusFilter === 'ativo' ? true : statusFilter === 'inativo' ? false : undefined
      });
      
      setPlanos(response.data || []);
      setTotalPages(response.pagination?.totalPages || 1);
    } catch (error) {
      console.error('Erro ao carregar planos:', error);
      toast.error('Erro ao carregar planos');
    } finally {
      setLoading(false);
    }
  }, [currentPage, itemsPerPage, searchTerm, statusFilter]);

  useEffect(() => {
    loadPlanos();
  }, [loadPlanos]);

  const handleContratar = async (plano: Plano) => {
    setSelectedPlano(plano);
    setIsModalOpen(true);
  };

  const confirmarContratacao = async () => {
    if (!selectedPlano) return;

    try {
      setContratando(true);
      const response = await services.compraPlano.comprar({
        planoId: selectedPlano.id
      });

      toast.success('Plano contratado com sucesso!');
      setIsModalOpen(false);
      setSelectedPlano(null);
      
      // Redirecionar para a página de plano atual
      router.push('/dashboard/empresa/planos/atual');
    } catch (error: any) {
      console.error('Erro ao contratar plano:', error);
      toast.error(error.response?.data?.error || 'Erro ao contratar plano');
    } finally {
      setContratando(false);
    }
  };

  const statusOptions = [
    { value: 'ativo', label: 'Ativos' },
    { value: 'inativo', label: 'Inativos' },
    { value: '', label: 'Todos' }
  ];

  return (
    <div className="p-4 sm:p-6 lg:p-8">
      <div className="mb-8">
        <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
          <div>
            <h1 className="text-2xl font-semibold text-gray-900 dark:text-white mb-2">
              Contratar Planos
            </h1>
            <p className="text-sm text-gray-500">
              Escolha o plano ideal para sua empresa
            </p>
          </div>
        </div>

        <div className="mt-6 flex flex-col sm:flex-row items-center gap-4">
          <div className="flex-1 w-full sm:max-w-xs">
            <Input
              type="text"
              placeholder="Buscar planos..."
              value={searchTerm}
              onChange={(e: React.ChangeEvent<HTMLInputElement>) => setSearchTerm(e.target.value)}
              className="w-full"
            />
          </div>
          <div className="flex flex-wrap gap-3">
            <Select
              placeholder="Status"
              options={statusOptions}
              value={statusOptions.find(opt => opt.value === statusFilter)}
              onChange={(option) => setStatusFilter((option as any)?.value || 'ativo')}
              className="whitespace-nowrap min-w-40"
            />
          </div>
        </div>
      </div>

      <div className="flex justify-between mb-4 gap-2 flex-wrap">
        <div className='flex items-center gap-0.5'>
          <Label className='flex items-center mb-0 h-full'>Quantidade por página</Label>
          <Input
            className='max-w-24'
            defaultValue={itemsPerPage}
            max={100}
            min={1}
            onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
              const newValue = Number(e.target.value) || 9;
              setItemsPerPage(newValue);
              setCurrentPage(1);
            }}
          />
        </div>
        <Pagination
          currentPage={currentPage}
          totalPages={totalPages}
          onPageChange={setCurrentPage}
        />
      </div>

      {loading ? (
        <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
          {[1, 2, 3].map((n) => (
            <div key={n} className="animate-pulse bg-gray-100 dark:bg-gray-800 rounded-lg h-48" />
          ))}
        </div>
      ) : (
        <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
          {planos.length > 0 ? (
            planos.map((plano) => (
              <Card key={plano.id} className="p-6">
                <div className="flex flex-col h-full">
                  <div className="flex-1">
                    <div className="flex items-center justify-between mb-4">
                      <h3 className="text-lg font-semibold text-gray-900 dark:text-white">
                        {plano.nome}
                      </h3>
                      <Badge
                        color={plano.status === 'ATIVO' ? 'success' : 'error'}
                        className="capitalize"
                      >
                        {plano.status === 'ATIVO' ? 'Ativo' : 'Inativo'}
                      </Badge>
                    </div>
                    
                    <div className="space-y-3 mb-6">
                      <div className="flex items-center gap-2">
                        <CreditCardIcon className="h-5 w-5 text-gray-400" />
                        <span className="text-sm text-gray-500 dark:text-gray-400">Valor:</span>
                        <span className="font-semibold text-gray-900 dark:text-white">
                          {formatCurrency(plano.valor)}
                        </span>
                      </div>
                      
                      <div className="flex items-center gap-2">
                        <ClockIcon className="h-5 w-5 text-gray-400" />
                        <span className="text-sm text-gray-500 dark:text-gray-400">Duração:</span>
                        <span className="font-semibold text-gray-900 dark:text-white">
                          {plano.dias} dias
                        </span>
                      </div>
                      
                      <div className="flex items-center gap-2">
                        <CheckCircleIcon className="h-5 w-5 text-gray-400" />
                        <span className="text-sm text-gray-500 dark:text-gray-400">Valor por dia:</span>
                        <span className="font-semibold text-gray-900 dark:text-white">
                          {formatCurrency(plano.valor / plano.dias)}
                        </span>
                      </div>
                    </div>
                  </div>
                  
                  <div className="mt-auto">
                    <Button
                      variant="primary"
                      onClick={() => handleContratar(plano)}
                      disabled={plano.status !== 'ATIVO'}
                      className="w-full"
                    >
                      {plano.status === 'ATIVO' ? 'Contratar Plano' : 'Indisponível'}
                    </Button>
                  </div>
                </div>
              </Card>
            ))
          ) : (
            <div className="col-span-full text-center py-12">
              <p className="text-gray-500 dark:text-gray-400">
                Nenhum plano encontrado
              </p>
            </div>
          )}
        </div>
      )}

      {totalPages > 1 && (
        <div className="mt-6">
          <div className="flex justify-end mb-4 gap-2 flex-wrap">
            <Pagination
              currentPage={currentPage}
              totalPages={totalPages}
              onPageChange={setCurrentPage}
            />
          </div>
        </div>
      )}

      <Modal
        isOpen={isModalOpen}
        onClose={() => {
          setIsModalOpen(false);
          setSelectedPlano(null);
        }}
        className="max-w-[500px] m-4"
      >
        <div className="no-scrollbar relative w-full overflow-y-auto rounded-3xl bg-white p-4 dark:bg-gray-900 lg:p-11">
          <div className="px-2 pr-14">
            <h4 className="mb-2 text-2xl font-semibold text-gray-800 dark:text-white/90">
              Confirmar Contratação
            </h4>
            <p className="mb-6 text-sm text-gray-500 dark:text-gray-400 lg:mb-7">
              Confirme os dados do plano que deseja contratar.
            </p>
          </div>
          
          {selectedPlano && (
            <div className="space-y-4 mb-6">
              <div className="bg-gray-50 dark:bg-gray-800 p-4 rounded-lg">
                <h5 className="font-semibold text-gray-900 dark:text-white mb-2">
                  {selectedPlano.nome}
                </h5>
                <div className="space-y-2 text-sm">
                  <div className="flex justify-between">
                    <span className="text-gray-500 dark:text-gray-400">Valor:</span>
                    <span className="font-semibold text-gray-900 dark:text-white">
                      {formatCurrency(selectedPlano.valor)}
                    </span>
                  </div>
                  <div className="flex justify-between">
                    <span className="text-gray-500 dark:text-gray-400">Duração:</span>
                    <span className="font-semibold text-gray-900 dark:text-white">
                      {selectedPlano.dias} dias
                    </span>
                  </div>
                  <div className="flex justify-between">
                    <span className="text-gray-500 dark:text-gray-400">Valor por dia:</span>
                    <span className="font-semibold text-gray-900 dark:text-white">
                      {formatCurrency(selectedPlano.valor / selectedPlano.dias)}
                    </span>
                  </div>
                </div>
              </div>
            </div>
          )}

          <div className="flex justify-end gap-3 pt-4">
            <Button
              type="button"
              variant="outline"
              onClick={() => {
                setIsModalOpen(false);
                setSelectedPlano(null);
              }}
              disabled={contratando}
            >
              Cancelar
            </Button>
            <Button
              type="button"
              variant="primary"
              onClick={confirmarContratacao}
              disabled={contratando}
            >
              {contratando ? 'Contratando...' : 'Confirmar Contratação'}
            </Button>
          </div>
        </div>
      </Modal>
    </div>
  );
}
